 import './App.css';
 import gptLogo from './assets/chatgpt.svg';
 import addBtn from './assets/add-30.png';
 import msgIcon from './assets/message.svg';
 import home from './assets/home.svg';
 import saved from './assets/bookmark.svg';
 import rocket from './assets/rocket.svg';
 import sendBtn from './assets/send.svg';
 import userIcon from './assets/user-icon.png';
 import gptImgLogo from './assets/chatgptLogo.svg';
 import {sendMsgToOpenAI} from './openai.js';
 import {useState} from 'react';



function App() {
  

  const [input, setInput] = useState('');
  const [messages, setMessages] = useState([
    {
    text: 'Hi, I am ChatPub. I am here to help you with your queries.',
    isBot : true,
  }
]);
  // chatgpt message
  const handleSend = async () => {
    const res = await sendMsgToOpenAI(input)
    setMessages([
      ...messages, 
      {text: input, isBot : false},
      {text: res, isBot : true}
    ])
    console.log(res)
  }

  return (
    <div className="App">
      {/* side bar */}
      <div className="sidebar">
        <div className = "upperSide">
          <div className='upperSideTop'><img src = {gptLogo} alt ="Logo" className='logo'/><span className='brand'>Chat Pub</span></div>
          <button className='midBtn'><img src ={addBtn} alt ='' className='addBtn'/>New Chat</button>
          <div className='upperSideBottom'>
            <button className='query'><img src ={msgIcon} alt ='Query'/>What is Programming?</button>
            <button className='query'><img src ={msgIcon} alt ='Query'/>What is Programming?</button>
          </div>
        </div>
        <div className = "lowerSide">
          <div className='listItems'><img src= {home} alt ="" className='listItemsImg'/>Home</div>
          <div className='listItems'><img src= {saved} alt ="" className='listItemsImg'/>Save</div>
          <div className='listItems'><img src= {rocket} alt ="" className='listItemsImg'/>Upgrade to Pro</div>
        </div>
      </div>

      {/* main */}
      <div className="main">
        <div className='chats'>
          <div className='chat'>
            <img className='chatImg' src = {userIcon} alt ="" /> <p className='txt'>Hello</p>
          </div>
          <div className='chat bot'>
            <img className='chatImg' src = {gptImgLogo} alt ="" /> <p className='txt'>lorem100</p>
          </div>
            {messages.map((message, i) => 
              <div key ={i} className={message.isBot?'chat bot':"chat"}>
                <img className='chatImg' src = {message.isBot?gptImgLogo:userIcon} alt ="" /> <p className='txt'>{message.text}</p>
              </div>
            )}

        </div>
        <div className='chatFooter'>
          <div className='inp'>
            <input type ="text" name="" id="" placeholder='Send a Message ...'value = {input} onChange={(e)=> {setInput(e.target.value)}} />
            
            <button className='send' onClick={handleSend}><img src ={sendBtn} alt ="Send"/></button>
          </div>

        </div>

      </div>


    </div>
  );
}

export default App;
